
import { defineComponent } from 'vue'
import { useRouter } from "vue-router"

export default defineComponent({
  name: 'uploadFile',
  components: {
  },
  setup(props, context){
    const router = useRouter()
    const handleLeft = () => {
      router.go(-1)
    }

    // 上传视频
    const handleUpload = (file:any) => {
      console.log("file---", file)
    }

    return {
      handleLeft,
      handleUpload
    }
  }
})
